.container{max-width:1000px;margin-right:auto;margin-left:auto;display:flex;justify-content:center;align-items:center;min-height:100vh}.table{width:100%;margin-bottom:50px}.table-header{display:flex;width:100%;background:#000}.table-row{display:flex;width:100%}.table-data,.header__item{flex:1 1 20%;text-align:center;line-height:30px}.header__item{text-transform:uppercase}.header__item.top{color:#fff}.filter__link{color:#fff;text-decoration:none;position:relative;display:inline-block}.filter__link::after{content:"";position:absolute;color:#fff;top:50%;transform:translateY(-50%)}.filter__link.desc::after{content:"(desc)"}.filter__link.asc::after{content:"(asc)"}.button:hover{cursor:pointer}.button{font-size:12px;font-weight:400;display:inline-block;color:#fff;border-radius:.25em;text-shadow:-1px -1px 0px rgba(0,0,0,.4)}.primary{line-height:32px;transition:ease-in-out .2s;padding:0 8px}.primary:hover,.condensed:hover,.touch:hover{transform:scale(1.02);box-shadow:2px 2px 5px rgba(0,0,0,.2),inset 0 0 0 99999px rgba(0,0,0,.2)}.touch{transition:ease-in-out .2s;line-height:32px;width:32px;padding:0px;text-align:center}.edit:before,.save:before,.new:before,.cancel:before,.delete:before{font-family:FontAwesome;display:inline-block;font-size:1rem;padding-right:12px;background:none;color:#fff}.touch.edit:before,.touch.save:before,.touch.new:before,.touch.cancel:before,.touch.delete:before{width:10%;text-align:center;font-size:14px}.inlineIcon.edit,.inlineIcon.save,.inlineIcon.new,.inlineIcon.cancel,.inlineIcon.delete{background:rgba(0,0,0,0)}.inlineIcon.edit:before,.inlineIcon.save:before,.inlineIcon.new:before,.inlineIcon.cancel:before,.inlineIcon.delete:before{line-height:32px;font-size:32px;padding:4px 0px}.edit{background:#3498db}.edit:before{content:""}.inlineIcon.edit:before{color:#3498db}.save{background:#2ecc71}.save:before{content:""}.inlineIcon.save:before{color:#2ecc71}.delete{background:#c0392b}.delete:before{content:""}.inlineIcon.delete:before{color:#c0392b}.cancel{background:#555}.cancel:before{content:""}.red{border:1px solid red}button.disabled{cursor:default;background-color:#ccc}button.disabled:hover{transform:scale(1);box-shadow:none}input:focus{outline:none;box-shadow:none}